<template>
    <div class="login">
        <div class="login-title">
            <p class="sub-title">歡迎登入</p>
            <p class="title">WELLCOME</p>
        </div>
        <div class="login-content">
            <div class="login-join">
                <p class="title">
                    Welcome To<br>Join Us !
                </p>
                <p class="sub-title">您尚未有帳號？</p>
                <p class="txt">請點擊<span>下方按鈕立即註冊</span>加入我們<br>（註冊時務必詳閱會員條款及會員權益說明）</p>
                <div class="btn">
                    <router-link class="join-btn member" to="/join">
                        <i class="el-icon-user-solid" /><br>
                        一般會員<br>
                        <span>MEMBER SING UP</span>
                    </router-link>
                    <router-link class="join-btn" to="/join">
                        <i class="el-icon-s-custom" /><br>
                        二手車商<br>
                        <span>AGENT SING UP</span>
                    </router-link>
                </div>
            </div>
            <div class="login-login">
                <div class="input">
                    <el-input v-model="Username" prefix-icon="el-icon-user-solid" suffix-icon="el-icon-question" placeholder="Username"></el-input>
                    <el-input v-model="Password" prefix-icon="el-icon-lock"  placeholder="Password" show-password></el-input>
                    <div class="forgot-box">
                        <el-checkbox v-model="checked">記住帳號密碼</el-checkbox>
                        <router-link to="/forget" class="pwd">忘記密碼</router-link>
                    </div>
                    <div class="login-btn" @click="goMember">確認登入</div>
                </div>
                <div class="dividing"><span />OR <span /></div>
                <div class="input">
                    <div class="login-btn fb"><img src="~@/assets/img/login/fb.png" alt="" srcset=""> Facebook 登入</div>
                    <div class="login-btn google"><img src="~@/assets/img/login/google.png" alt="" srcset="">  Google 登入</div>
                </div>
                

            </div>
        </div>
        


    </div>
</template>

<script>
export default {
    name:'login',
    data() {
        return{
            Username:'',
            Password:'',
            checked:false,

        }
    },
    methods: {
        goMember() {
            this.$router.push(`/member/favorite`)
        }
    }

}
</script>

<style lang="scss">
.login {
    padding: 105px 0;
    .login-title {
        background: url('~@/assets/img/login/login-banner.png') no-repeat center;
        background-size: cover;
    }
    .login-content {
        display: flex;
        .login-join {
            width: 40%;
            height: auto;
            background: linear-gradient(to bottom, #ffffff 0%,#f4f4f4 22%,#f4f4f4 72%,#ffffff 100%);
            text-align: center;
            padding: 200px 0;
            color: #555555;
            .title {
                color: #ef6d38;
                font-size: 60px;
                font-weight: bold;
            }
            .sub-title {
                font-size: 30px;
                font-weight: bold;
                margin: 30px 0 20px 0;
            }
            .txt {
                line-height: 30px;
                margin-bottom: 50px;
                span {
                    color: #ef6d38;
                }
            }
            .btn {
                display: flex;
                justify-content: center;
                .join-btn {
                    width: 150px;
                    height: 150px;
                    background: #555555;
                    border-radius: 20px;
                    color: #fff;
                    margin:0 20px;
                    font-size: 24px;
                    line-height: 20px;
                    i {
                        font-size: 50px;
                        margin: 10% 0;
                    }
                    span {
                        font-size: 14px;
                    }
                }
                .member {
                    background: #ef6d38;
                }
            }
        }
        .login-login {
            padding: 200px 0;
            width: 60%;
            .input {
                width: 400px;
                margin: auto;
            }
            .forgot-box {
                font-size: 14px;
                padding: 0 25px;
                display: flex;
                justify-content: space-between;
                align-items: baseline;
                margin-bottom: 50PX;
                .pwd {
                    text-decoration: underline;
                }
            }
            .login-btn {
                width: 100%;
                line-height: 70px;
                border-radius: 20px;
                text-align: center;
                background: #555555;
                color: #fff;
                font-size: 20px;
                cursor: pointer;
                transition: all .3s;
                position: relative;
                overflow: hidden;
                &:hover {
                    filter: brightness(130%)
                }
            }
            .fb {
                background: #436092;
                margin-bottom: 30px;

            }
            .google {
                background: #df443c;
            }
            .fb,.google {
                img {
                    background: #00000025;
                    padding: 18px;
                    position: absolute;
                    left: 0;
                }
            }
            .dividing {
                display: flex;
                justify-content: center;
                align-items: center;
                color: #a7a7a7;
                margin: 50px 0;
                span {
                    height: 1px;
                    background: #a7a7a7;
                    width: 200px;
                    display: block;
                    margin: 0 20px;
                }
            }
        }
        @media screen and (max-width: 900px){  
            flex-direction: column-reverse;
            .login-join ,.login-login{
                width: 100%;
                padding: 30% 5%;
                box-sizing: border-box;
            }
            .login-login {
                .input {
                    width: 80%;
                    max-width: 400px;
                }
            }
        }
        @media screen and (max-width: 500px){  
            .login-join {
                .btn .join-btn {
                    width: 30vmin;
                    height: 30vmin;
                    font-size: 4vmin;
                    i {
                        font-size: 13vmin;
                        margin: 8% 0;
                    }
                    span {
                        font-size: 3vmin;
                    }
                }
            }
            .login-login {
                .input {
                    width: 80%;
                }
            }
        }
    }    
}

</style>


